export const savedLPOsStateStorage = () => {
  const lattestSavedLPOs = JSON.parse(
    localStorage.getItem("Saved Proforma Invoice")
  );

  return lattestSavedLPOs;
};

export const pushUploadedLpo2Storage = (invoice) => {
  localStorage.setItem("toggle_exchange_rate_form", JSON.stringify(false));
  localStorage.setItem("toggle_form2", JSON.stringify(false));
  localStorage.setItem("currency", JSON.stringify(invoice.currency));
  localStorage.setItem("exchange_rate", JSON.stringify(invoice.exchange_rate));
  localStorage.setItem(
    "proforma_invoice",
    JSON.stringify(invoice.proforma_invoice)
  );
  localStorage.setItem("quotations", JSON.stringify(invoice.quotations));
  localStorage.setItem(
    "quotations_length",
    JSON.stringify(invoice.quotations_length)
  );
  localStorage.setItem("displayRecalculateBTN", JSON.stringify(false));
  localStorage.setItem("saveProformaInvoice", JSON.stringify(false));
};

export const delete4rmStorageSavedLpo = (LPOs) => {
  const defaultSavedProformaInvoice = JSON.parse(
    localStorage.getItem("Saved Proforma Invoice")
  );

  const newlySavedProformaInvoice = defaultSavedProformaInvoice.filter(
    (lpoS) => lpoS.proforma_invoice.lpoItemName !== LPOs.lpoItemName
  );

  localStorage.setItem(
    "Saved Proforma Invoice",
    JSON.stringify(newlySavedProformaInvoice)
  );

  return newlySavedProformaInvoice;
};
