import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { addExchangeRate, getExchangeRate } from "../../actions/currencyTypes";
import { getStorageExchangeRate } from "../../actions/currencyStorageActions";
import Form from "./Form";
import Form2 from "./Form2";

import classnames from "classnames";
import { getStorageQuotations } from "../../actions/quotationStorageActions";
import { getQuotations } from "../../actions/quotationTypes";

import Big from "big.js";

import ReactGA from "react-ga";

import { toast } from "react-toastify";

class QuotesForm extends Component {
  state = {
    toggle1: false,
    toggle2: false,
    naira: "",
    dollar: "",
    exchangedNaira: "",
    exchangedDollar: "",
  };

  componentDidMount() {
    this.props.getExchangeRate();
  }

  UNSAFE_componentWillReceiveProps(props) {
    const exchangeRate = getStorageExchangeRate();

    this.setState({
      exchangedNaira: exchangeRate.naira,
      exchangedDollar: exchangeRate.dollar,
    });
  }

  toggle1 = () => {
    this.setState({ toggle1: !this.state.toggle1 });

    // Manage Exchange Form Toggle
    if (this.state.toggle1 === true) {
      localStorage.setItem("toggle_exchange_rate_form", JSON.stringify(false));
    } else if (this.state.toggle1 === false) {
      localStorage.setItem("toggle_exchange_rate_form", JSON.stringify(true));
    }
  };

  toggle2 = () => {
    this.setState({ toggle2: !this.state.toggle2 });

    // Manage Exchange Form Toggle
    if (this.state.toggle2 === true) {
      localStorage.setItem("toggle_form2", JSON.stringify(false));
    } else if (this.state.toggle2 === false) {
      localStorage.setItem("toggle_form2", JSON.stringify(true));
    }
  };

  valueChanged = (e) => {
    this.setState({ [e.target.name]: e.target.value });
  };

  submitExchangeRate = (e) => {
    e.preventDefault();

    let { naira, dollar } = this.state;

    // Get All Quotations From Storage
    const getAllQuotation = getStorageQuotations();
    let quotationExchangeRate;

    // Ensure New ExchangeRate Value Isn't Zero (0)
    if (Number(naira) <= 0 || Number(dollar) <= 0) {
      alert("New Exchange Rate Value Must Be Greater Than 0");
    } else {
      // Report Button Click To Google Analytics
      ReactGA.event({
        category: "Button",
        action: "Clicked CHANGE EXCHANGE RATE BTN",
        label: "QuotesForm",
      });

      let newExchangeRateRatio;
      if (Number(naira) === Number(dollar)) {
        newExchangeRateRatio = Number(naira) / Number(dollar);
      } else if (Number(naira) > Number(dollar)) {
        newExchangeRateRatio = Number(naira) / Number(dollar);
      } else if (Number(dollar) > Number(naira)) {
        newExchangeRateRatio = Number(dollar) / Number(naira);
      }

      // Prevent Execution If No Expenses
      if (getAllQuotation.length !== 0) {
        // eslint-disable-next-line no-restricted-globals
        let confirmBaseCurrency = confirm(
          "--------- RETAIN DOLLAR (USD) AS THE BASE CURRENCY --------- \n \n The Base Currency Would Be The Constant Currency In Which The 2nd Currency Would Be Recalculated From... \n \n --------- BASE CURRENCY DETAILS --------- \n DOLLAR = OK \n NAIRA = Cancel"
        );

        // eslint-disable-next-line no-unused-vars
        let baseCurrency;

        if (confirmBaseCurrency) {
          baseCurrency = "Dollar";

          quotationExchangeRate = getAllQuotation.map((quotations) => ({
            id: quotations.id,
            itemName: quotations.itemName,
            itemDescription: quotations.itemDescription,
            itemUnit: quotations.itemUnit,
            /*
             **  If New Exchange Rate Ratio Is Higher Than Storage Exchange Rate Ratio,
             **  Multiply The New Exchange Rate Ratio With Dollar To Get Naira Value.
             **  This Occures If Dollar Value Is Greater Than Naira Value, Else Vise-Visa
             */
            itemPriceNaira:
              dollar < naira
                ? Big(quotations.itemPriceDollar)
                    .times(newExchangeRateRatio)
                    .toFixed()
                : Big(quotations.itemPriceDollar)
                    .div(newExchangeRateRatio)
                    .toFixed(),
            itemPriceDollar: quotations.itemPriceDollar,
          }));
        } else {
          baseCurrency = "Naira";

          quotationExchangeRate = getAllQuotation.map((quotations) => ({
            id: quotations.id,
            itemName: quotations.itemName,
            itemDescription: quotations.itemDescription,
            itemUnit: quotations.itemUnit,
            itemPriceNaira: quotations.itemPriceNaira,
            /*
             **  If New Exchange Rate Ratio Is Higher Than Storage Exchange Rate Ratio,
             **  Divide The New Exchange Rate Ratio With Naira To Get Dollar Value.
             **  This Occures If Dollar Value Is Greater Than Naira Value, Else Vise-Visa
             */
            itemPriceDollar:
              dollar < naira
                ? Big(quotations.itemPriceNaira)
                    .div(newExchangeRateRatio)
                    .toFixed()
                : Big(quotations.itemPriceNaira)
                    .times(newExchangeRateRatio)
                    .toFixed(),
          }));
        }
        localStorage.setItem(
          "quotations",
          JSON.stringify(quotationExchangeRate)
        );
        this.props.getQuotations();

        // Reveal the Recalculate BTN only when there is need for recalculation
        document.getElementById("recalculateInvoice").style.display = "block";
        // Set displayRecalculateBTN in LS to true
        localStorage.setItem("displayRecalculateBTN", true);
        // Hide saveProformaInvoice BTN
        document.getElementById("saveProformaInvoice").style.display = "none";
        // Set saveProformaInvoice in LS to false
        localStorage.setItem("saveProformaInvoice", false);
      }
      this.setState({ naira: "", dollar: "" });
      this.props.addExchangeRate({ naira, dollar });

      toast.success(
        `Exchange Rate Successfull Changed To $${dollar} : ${naira} `
      );
    }
  };

  render() {
    const {
      toggle1,
      toggle2,
      naira,
      dollar,
      exchangedNaira,
      exchangedDollar,
    } = this.state;
    return (
      <div>
        <div
          style={{
            backgroundImage:
              "linear-gradient(lightgray 5%, white 12%,gray 5%,  lightgray 60%)",
            transform: "rotate(180deg)",
          }}
        >
          <br />
        </div>
        <div
          className="container-fluid pt-3 pb-3 px-5 mb-1"
          // Main Background Styling
          style={{
            backgroundImage:
              "linear-gradient(lightgray 2%, gray 15%, lightgray 90%)",
            transform: "rotate(180deg)",
          }}
        >
          <div
            className="row"
            // Inner Background Styling
            style={{
              backgroundImage:
                "linear-gradient(lightgray 1%, white 30%, rgb(187, 202, 131) 100%)",
              transform: "rotate(180deg)",
              paddingBottom: "10px",
              boxShadow: "0px 2px 4px rgba(0, 0, 0, .5)",
            }}
          >
            <div className="col-sm-7">
              <h4 className="text-center text-secondary">
                <i className="fa fa-align-center fa-sm" /> STEP 1
              </h4>
              <div>
                <label>
                  <b>Enter Quotation Details...</b>
                </label>
                {toggle1 ? (
                  <form
                    onSubmit={this.submitExchangeRate}
                    id="form2"
                    className="input-group mb-3"
                    style={boxShadows}
                  >
                    <div className="input-group-append">
                      <span className="input-group-text bg-dark text-white">
                        {exchangedNaira}
                      </span>
                    </div>
                    <input
                      type="number"
                      step="0.02"
                      name="naira"
                      className="form-control"
                      placeholder="Nigerian Naira (NGN)"
                      value={naira}
                      onChange={this.valueChanged}
                    />
                    <button className="btn btn-sm btn-info" style={boxShadows}>
                      CHANGE EXCHANGE RATE
                    </button>
                    <input
                      type="number"
                      step="0.02"
                      name="dollar"
                      className="form-control"
                      placeholder="US Dollar (USD)"
                      value={dollar}
                      onChange={this.valueChanged}
                    />
                    <div className="input-group-append">
                      <span className="input-group-text bg-dark text-white">
                        ${exchangedDollar}
                      </span>
                    </div>
                  </form>
                ) : null}
                <div
                  onClick={this.toggle1}
                  id="setExchangeRateBtn"
                  className="btn btn-outline-primary btn-sm float-right mb-2"
                  style={boxShadows}
                >
                  Set Exchange Rate
                </div>
              </div>
              <Form
                placeholder1="e.g. Graphics Design"
                inputGroupText1="Item Name"
                placeholder2="e.g. Designing Flyer Artwork Based On Spec"
                inputGroupText2="Description"
                placeholder4="(1 Unit is the default value)"
                inputGroupText4="Item Unit"
              />
            </div>
            <div className="col-sm-5 mt-4 pt-2">
              <h4 className="text-center text-secondary">
                <i className="fa fa-align-center fa-sm" /> STEP 2
              </h4>
              <button
                id="generateInvoice"
                className={classnames("btn form-control", {
                  "btn-secondary": !this.state.toggle2,
                  "btn-outline-success": this.state.toggle2,
                })}
                style={boxShadows}
                onClick={this.toggle2}
              >
                Generate Invoice
              </button>
              {toggle2 ? (
                <div className="mt-1 col-sm-12">
                  <Form2
                    placeholder1="% (0 is the default value)"
                    inputGroupText1="VAT (in %)"
                    placeholder2="e.g. 25%"
                    inputGroupText2="Service Charge (in %)"
                    placeholder3="Total Job (Unit Number)"
                    inputGroupText3="(000's)"
                    placeholder4="Job Name"
                    inputGroupText4="LPO Item Name"
                  />
                </div>
              ) : null}
            </div>
          </div>
        </div>
        <div
          style={{
            backgroundImage:
              "linear-gradient(lightgray 5%, white 12%,gray 5%,  lightgray 60%)",
            marginTop: "-2px",
            marginBottom: "7px",
          }}
        >
          <br />
        </div>
      </div>
    );
  }
}

QuotesForm.propTypes = {
  addExchangeRate: PropTypes.func.isRequired,
  getExchangeRate: PropTypes.func.isRequired,
  getQuotations: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => ({
  exchangeRate: state.quotation.exchangeRate,
});

export default connect(mapStateToProps, {
  addExchangeRate,
  getExchangeRate,
  getQuotations,
})(QuotesForm);

const boxShadows = {
  boxShadow: "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
};
