import React from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";

import QuotesCardModalAccordion from "./QuotesCardModalAccordion";

import { savedLPOsState } from "../../actions/lpoTypes";

class QuotesCardModal extends React.Component {
  state = {
    defaultSavedProformaInvoice: "",
    LPOs: "",
  };

  componentDidMount() {
    // Load Saved Proforma Invoice To Modal
    this.props.savedLPOsState();
  }

  static getDerivedStateFromProps(props, state) {
    let toggle_exchange_rate_form = [];
    let toggle_form2 = [];
    let currency = [];
    let exchange_rate = [];
    let proforma_invoice = [];
    let quotations = [];
    let quotations_length = [];
    let displayRecalculateBTN = [];
    let saveProformaInvoice = [];

    // Prevents Application From Init Start Error
    if (localStorage.getItem("Saved Proforma Invoice") === null) {
      localStorage.setItem("Saved Proforma Invoice", "[]");
    }

    // Get Saved Proforma Invoice From LS
    const savedProformaInvoice = props.savedLPOsInState;

    savedProformaInvoice.forEach((quotes) => {
      toggle_exchange_rate_form.push(quotes.toggle_exchange_rate_form);
      toggle_form2.push(quotes.toggle_form2);
      currency.push(quotes.currency);
      exchange_rate.push(quotes.exchange_rate);
      proforma_invoice.push(quotes.proforma_invoice);
      quotations.push(quotes.quotations);
      quotations_length.push(quotes.quotations_length);
      displayRecalculateBTN.push(quotes.displayRecalculateBTN);
      saveProformaInvoice.push(quotes.saveProformaInvoice);
    });

    /*
    Create Variables For Indevidual LPO And
    Rearrange All Variables Into Their Individual LPOs
    */
    let lpo = [];
    function createVariables() {
      for (let i = 0; i < proforma_invoice.length; ++i) {
        lpo[i] = [];

        quotations.forEach((quote, index) => {
          if (index === i) {
            lpo[i].push(quote);
          }
        });

        proforma_invoice.forEach((invoice, index) => {
          if (index === i) {
            lpo[i].push(invoice);
          }
        });

        exchange_rate.forEach((fx, index) => {
          if (index === i) {
            lpo[i].push(fx);
          }
        });
      }

      return lpo;
    }
    const createVariablesArr = createVariables();

    // Conbine All Objects Within Individual LPO To One Object
    let lpoIndex = {};
    createVariablesArr.forEach((createVariables, index) => {
      lpoIndex[index] = {};
      createVariables.forEach((variables) => {
        lpoIndex[index] = Object.assign({}, lpoIndex[index], variables);
      });
      return lpoIndex;
    });

    const allProcessedLpoInvoice = [];
    let idIncreament = 0;
    // Loop Through The LPO Objects
    Object.entries(lpoIndex).forEach(([key, valueO], index) => {
      for (let x = 0; x < Object.keys(lpoIndex).length; x++) {
        let allExpenses = [];
        // let allExpenses;
        let lpoObj = {};
        if (index === x) {
          Object.entries(valueO).forEach(([key, value]) => {
            // For LPO Details
            if (value.toString() !== "[object Object]") {
              lpoObj[key] = value;
            }

            // For Quotation Expenses Details
            // Conbine All Objects Within Individual LPO To One Object
            if (value.toString() === "[object Object]") {
              let quotationIndexObj = {};
              Object.entries(value).forEach(([key, valueV], indexV) => {
                // Name valueV
                let discriptionName;
                if (indexV === 0) {
                  discriptionName = "id";
                } else if (indexV === 1) {
                  discriptionName = "itemName";
                } else if (indexV === 2) {
                  discriptionName = "itemDescription";
                } else if (indexV === 3) {
                  discriptionName = "itemUnit";
                } else if (indexV === 4) {
                  discriptionName = "itemUnitPriceNaira";
                } else if (indexV === 5) {
                  discriptionName = "itemUnitPriceDollar";
                }

                quotationIndexObj[discriptionName] = valueV;
                return quotationIndexObj;
              });
              // Push Expenses Objects To All Expenses Array
              allExpenses.push(quotationIndexObj);
            }
          });

          //   const processedLpoInvoice = { lpoObj, allExpenses };
          const processedLpoInvoice = Object.assign({}, lpoObj);
          processedLpoInvoice.expenses = allExpenses;

          idIncreament++;
          processedLpoInvoice.id = idIncreament;

          allProcessedLpoInvoice.push(processedLpoInvoice);
        }
      }
    });

    state.defaultSavedProformaInvoice = savedProformaInvoice;
    state.LPOs = allProcessedLpoInvoice;

    return null;
  }

  render() {
    const { defaultSavedProformaInvoice, LPOs } = this.state;
    return (
      // The Modal
      <div className="modal fade" id="QuotesCardModal">
        <div className="modal-dialog modal-dialog-scrollable">
          <div className="modal-content">
            {/*  Modal Header */}
            <div className="modal-header bg-info text-light">
              <h4 className="modal-title">
                Upload From Saved LPO <br />
                <sup style={{ color: "lightgray" }}>v4.0.0</sup>
              </h4>
              <button
                type="button"
                id="closeModal"
                className="close btn-outline-danger"
                data-dismiss="modal"
              >
                &times;
              </button>
            </div>

            {/*  Modal body  */}
            <div className="modal-body">
              <QuotesCardModalAccordion
                defaultSavedProformaInvoice={defaultSavedProformaInvoice}
                LPOs={LPOs}
              />
            </div>

            {/* Modal footer */}
            <div
              className="modal-footer"
              style={{ backgroundColor: "lightgray" }}
            >
              <button
                type="button"
                className="btn btn-danger"
                data-dismiss="modal"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

QuotesCardModal.propType = {
  savedLPOsInState: PropTypes.array.isRequired,
};

const mapStateToProps = (state) => ({
  savedLPOsInState: state.quotation.savedLPOsInState,
});

export default connect(mapStateToProps, {
  savedLPOsState,
})(QuotesCardModal);
