import React from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { addProformaInvoice } from "../../actions/quotationTypes";

import { getStorageQuotations } from "../../actions/quotationStorageActions";
import { toggleStorageExchangeRateForm } from "../../actions/currencyStorageActions";

import ReactGA from "react-ga";

import Big from "big.js";

class Form2 extends React.Component {
  state = {
    lpoItemName: "",
    totalJobUnit: "",
    serviceCharge: "",
    vat: "",
    discount: "0",
  };

  valueChanged = (e) => {
    this.setState({ [e.target.name]: e.target.value });
  };

  valueSubmited = (e) => {
    e.preventDefault();

    let {
      lpoItemName,
      totalJobUnit,
      serviceCharge,
      vat,
      discount,
    } = this.state;

    // If VAT Is Empty, Make  VAT (0)
    if (vat === "") {
      vat = "0";
    }

    const quotations = getStorageQuotations();

    // Only Run If There Are Expences
    if (quotations.length !== 0) {
      // Calculate Base On Current Currency
      let totalQuoteAmount;
      if (JSON.parse(localStorage.getItem("currency")).senior === "Naira") {
        totalQuoteAmount = quotations
          .map((quotation) =>
            Big(quotation.itemUnit).times(quotation.itemPriceNaira).toFixed()
          )
          .reduce((acc, value) => Big(acc).plus(value).toFixed());
      } else if (
        JSON.parse(localStorage.getItem("currency")).senior === "Dollar"
      ) {
        totalQuoteAmount = quotations
          .map((quotation) =>
            Big(quotation.itemUnit).times(quotation.itemPriceDollar).toFixed()
          )
          .reduce((acc, value) => Big(acc).plus(value).toFixed());
      }

      const proformaInvioce = {
        totalQuoteAmount: Big(totalQuoteAmount).toFixed(),
        lpoItemName,
        totalJobUnit: Big(totalJobUnit).toFixed(),
        serviceCharge: Big(serviceCharge).toFixed(),
        vat: Big(vat).toFixed(),
        discount,
      };

      this.props.addProformaInvoice(proformaInvioce);

      // Report Button Click To Google Analytics
      ReactGA.event({
        category: "Button",
        action: "Clicked Calculate BTN",
        label: "Form2",
      });

      this.setState({
        lpoItemName: "",
        totalJobUnit: "",
        serviceCharge: "",
        vat: "",
        discount: "0",
      });
      // Show Clear Invoice & Recalculate BTN
      document.getElementById("clearInvoice").style.display = "block";
      document.getElementById("recalculateInvoice").style.display = "none";
      // Set recalculateInvoice in LS to false
      localStorage.setItem("displayRecalculateBTN", false);
      // Set saveProformaInvoice in LS to true
      localStorage.setItem("saveProformaInvoice", true);
      // Display saveProformaInvoice BTN
      document.getElementById("saveProformaInvoice").style.display = "block";
      // Close Form
      document.getElementById("generateInvoice").click();
    } else {
      alert("You  Must Enter At Least 1 Expense In (STEP 1)");
    }
  };

  toggleExchangeRateForm = () => {
    if (toggleStorageExchangeRateForm() === true) {
      // Colapse Exchange Rate Form
      document.getElementById("setExchangeRateBtn").click();
    }
  };

  render() {
    const {
      lpoItemName,
      totalJobUnit,
      serviceCharge,
      vat,
      discount,
    } = this.state;

    const {
      placeholder1,
      inputGroupText1,
      placeholder2,
      inputGroupText2,
      placeholder3,
      inputGroupText3,
      placeholder4,
      inputGroupText4,
    } = this.props;
    return (
      <form
        onSubmit={this.valueSubmited}
        onFocus={this.toggleExchangeRateForm}
        className="card card-header table-responsive"
      >
        <div className="input-group mb-3" style={boxShadows}>
          <input
            required
            type="text"
            name="lpoItemName"
            className="form-control"
            placeholder={placeholder4}
            value={lpoItemName}
            onChange={this.valueChanged}
          />
          <div className="input-group-prepend" style={boxShadows}>
            <span className="input-group-text">{inputGroupText4}</span>
          </div>
        </div>

        <div className="input-group mb-3" style={boxShadows}>
          <div className="input-group-prepend" style={boxShadows}>
            <span className="input-group-text">{inputGroupText3}</span>
          </div>
          <input
            required
            type="number"
            name="totalJobUnit"
            className="form-control"
            placeholder={placeholder3}
            value={totalJobUnit}
            onChange={this.valueChanged}
          />
        </div>

        <div className="input-group mb-3" style={boxShadows}>
          <input
            required
            type="number"
            step="0.02"
            name="serviceCharge"
            className="form-control"
            placeholder={placeholder2}
            value={serviceCharge}
            onChange={this.valueChanged}
          />
          <div className="input-group-append" style={boxShadows}>
            <span className="input-group-text">{inputGroupText2}</span>
          </div>
        </div>

        <div className="input-group mb-3" style={boxShadows}>
          <div className="input-group-prepend" style={boxShadows}>
            <span className="input-group-text">{inputGroupText1}</span>
          </div>
          <input
            type="number"
            step="0.1"
            name="vat"
            className="form-control"
            placeholder={placeholder1}
            value={vat}
            onChange={this.valueChanged}
          />
        </div>

        <div className="mx-auto my-3 pointerScale">
          <select
            name="discount"
            value={discount}
            onChange={this.valueChanged}
            className="custom-select-sm bg-secondary text-white pointerScale"
            style={boxShadows}
          >
            <option defaultValue="0" className="bg-success">
              Grant Discount On Bulk Job (Optional)
            </option>
            <option value="2" style={{ backgroundColor: "gray" }}>
              2% Discount
            </option>
            <option value="3">3% Discount</option>
            <option value="4" style={{ backgroundColor: "gray" }}>
              4% Discount
            </option>
            <option value="5">5% Discount</option>
            <option value="6" style={{ backgroundColor: "gray" }}>
              6% Discount
            </option>
            <option value="7">7% Discount</option>
            <option value="8" style={{ backgroundColor: "gray" }}>
              8% Discount
            </option>
            <option value="9">9% Discount</option>
            <option value="10" style={{ backgroundColor: "gray" }}>
              10% Discount
            </option>
            <option value="11">11% Discount</option>
            <option value="12" style={{ backgroundColor: "gray" }}>
              12% Discount
            </option>
            <option value="13">13% Discount</option>
            <option value="14" style={{ backgroundColor: "gray" }}>
              14% Discount
            </option>
            <option value="15">15% Discount</option>
            <option value="16" style={{ backgroundColor: "gray" }}>
              16% Discount
            </option>
            <option value="17">17% Discount</option>
            <option value="18" style={{ backgroundColor: "gray" }}>
              18% Discount
            </option>
            <option value="19">19% Discount</option>
            <option value="20" style={{ backgroundColor: "gray" }}>
              20% Discount
            </option>
            <option value="21">21% Discount</option>
            <option value="22" style={{ backgroundColor: "gray" }}>
              22% Discount
            </option>
            <option value="23">23% Discount</option>
            <option value="24" style={{ backgroundColor: "gray" }}>
              24% Discount
            </option>
            <option value="25">25% Discount</option>
          </select>
        </div>

        <div className="row">
          <div className="col-sm-6 mx-auto p-1" style={boxShadows}>
            <input
              type="submit"
              value="Calculate"
              className="form-control btn btn-success btn-sm"
              style={boxShadows}
            />
          </div>
        </div>
      </form>
    );
  }
}

Form2.propTypes = {
  addProformaInvoice: PropTypes.func.isRequired,
};

export default connect(null, { addProformaInvoice })(Form2);

const boxShadows = {
  boxShadow: "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
};
