import React, { Component } from "react";
import ReactGA from "react-ga";

class Footer extends Component {
  render() {
    // Set Footer Year
    const thisYear = new Date("2020").getFullYear();
    let presentYear = new Date().getFullYear();
    return (
      <footer
        className="container pointerScale mb-2"
        style={{
          color: "gray",
          textAlign: "center",
          marginTop: "58px",
          marginBottom: "7px",
          paddingBottom: "7px",
        }}
      >
        <div className="row">
          <div className="col-sm-10">
            Copyright (c){" "}
            <span id="copyRightYear">
              {thisYear === presentYear
                ? thisYear
                : thisYear < presentYear
                ? thisYear + " - " + presentYear
                : null}{" "}
            </span>{" "}
            Saheed Odulaja - All Rights Reserved <br />
            <div>
              Get Source Code &lt; v2 From:{" "}
              <a
                href="https://github.com/Sidodus/Sidodus-proforma-invoice-calculator/"
                className="text-secondary"
              >
                https://github.com/Sidodus/Sidodus-proforma-invoice-calculator/
              </a>
            </div>
          </div>
          <div className="col-sm-2 mt-2">
            {/* Button to Open the Modal  */}
            <button
              type="button"
              className="btn btn-outline-secondary btn-sm float-left"
              data-toggle="modal"
              data-target="#myModal"
              onClick={() => {
                // Report Modal View To Google Analytics
                ReactGA.modalview("/Formula Modal");
              }}
            >
              Formula
            </button>
          </div>
        </div>
      </footer>
    );
  }
}

export default Footer;
