import React, { Component } from "react";
import QuotesCardModalAccordionLpoDetails from "./QuotesCardModalAccordionLpoDetails";
import QuotesCardModalAccordionLpoExpenses, {
  QuotesCardModalAccordionLpoExpensesHeader,
} from "./QuotesCardModalAccordionLpoExpenses";

import classnames from "classnames";

class QuotesCardModalAccordion extends Component {
  state = {
    defaultSavedProformaInvoice: "",
    lpoArr: "",
    newLpoArr: [],
  };

  UNSAFE_componentWillReceiveProps(props) {
    const { defaultSavedProformaInvoice, LPOs } = props;

    let lpo = [];

    LPOs.map((element) => {
      return lpo.push(element);
    });

    this.setState({
      defaultSavedProformaInvoice,
      lpoArr: props.LPOs,
      newLpoArr: lpo,
    });
  }

  render() {
    const { defaultSavedProformaInvoice, newLpoArr } = this.state;

    return (
      <React.Fragment>
        <div id="accordion">
          {newLpoArr.map((element) => (
            <div key={element.id}>
              <div className="card">
                <div className="card-header">
                  <QuotesCardModalAccordionLpoExpensesHeader
                    defaultSavedProformaInvoice={defaultSavedProformaInvoice}
                    LPOs={element}
                  />
                </div>
                <div
                  id={`collapse${element.id}`}
                  className={classnames("collapse", {
                    show: element.id === 1,
                  })}
                  data-parent="#accordion"
                >
                  <div className="card-body">
                    <QuotesCardModalAccordionLpoDetails
                      defaultSavedProformaInvoice={defaultSavedProformaInvoice}
                      LPOs={element}
                      dateSaved={defaultSavedProformaInvoice.map((date) =>
                        element.lpoItemName ===
                        date.proforma_invoice.lpoItemName
                          ? date.dateSaved
                          : null
                      )}
                      timeSaved={defaultSavedProformaInvoice.map((time) =>
                        element.lpoItemName ===
                        time.proforma_invoice.lpoItemName
                          ? time.timeSaved
                          : null
                      )}
                    />
                    <hr style={boxShadows} />
                    <QuotesCardModalAccordionLpoExpenses LPOs={element} />
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>
      </React.Fragment>
    );
  }
}

export default QuotesCardModalAccordion;

const boxShadows = {
  boxShadow: "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
};
