import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import {
  getProformaInvoice,
  addProformaInvoice,
} from "../../actions/quotationTypes";
import { savedLPOsState } from "../../actions/lpoTypes";

import { JSnumberToWordProcessor } from "../JSnumberToWordProcessor/JSnumberToWordProcessor";
import {
  getStorageQuotations,
  getStorageProformaInvoice,
} from "../../actions/quotationStorageActions";
import { getCurrency } from "../../actions/currencyTypes";
import { getStorageCurrency } from "../../actions/currencyStorageActions";

import ReactGA from "react-ga";

import Big from "big.js";

import classnames from "classnames";

import { toast } from "react-toastify";

class QuotesCard extends Component {
  state = {
    proforma: "1",
    storageCurrency: {},

    lpoItemName: "",

    totalJobUnit: "",
    totalJobUnit0: {},
    serviceCharge: "",
    serviceCharge0: {},
    serviceCharge1: {},
    totalQuoteAmount: "",
    totalQuoteAmount0: {},
    totalQuoteAmount1: {},
    discount: "",
    vat: "",
    vat0: {},
    vat1: {},
    profit: "",
    profit0: {},
    profit1: {},
    totalValue: "",
    totalValue0: {},
    totalValue1: {},
    discountPayable: "",
    discountPayable0: {},
    discountPayable1: {},
    vatPayable: "",
    vatPayable0: {},
    vatPayable1: {},
    totalPayableWithVat: "",
    totalPayableWithVat0: {},
    totalPayableWithVat1: {},
    unitDiscount: "",
    unitDiscount0: {},
    unitDiscount1: {},
    unitProfit: "",
    unitProfit0: {},
    unitProfit1: {},
    unitPrice: "",
    unitPrice0: {},
    unitPrice1: {},
    unit1000Price: "",
    unit1000Price0: {},
    unit1000Price1: {},
  };

  componentDidMount() {
    // Reveal the Recalculate BTN only when there is need for recalculation
    const displayRecalculateBTN = JSON.parse(
      localStorage.getItem("displayRecalculateBTN")
    );
    // Reveal the saveProformaInvoice BTN only when there is no need for recalculation
    const saveProformaInvoice = JSON.parse(
      localStorage.getItem("saveProformaInvoice")
    );

    if (displayRecalculateBTN === false) {
      document.getElementById("recalculateInvoice").style.display = "none";
    }

    if (saveProformaInvoice === false) {
      document.getElementById("saveProformaInvoice").style.display = "none";
    }

    this.props.getProformaInvoice();
    this.props.getCurrency();
  }

  UNSAFE_componentWillReceiveProps(props) {
    // Reveal the Recalculate BTN only when there is need for recalculation
    const displayRecalculateBTN = JSON.parse(
      localStorage.getItem("displayRecalculateBTN")
    );
    // Reveal the saveProformaInvoice BTN only when there is no need for recalculation
    const saveProformaInvoice = JSON.parse(
      localStorage.getItem("saveProformaInvoice")
    );

    if (displayRecalculateBTN === false) {
      document.getElementById("recalculateInvoice").style.display = "none";
    }

    if (saveProformaInvoice === false) {
      document.getElementById("saveProformaInvoice").style.display = "none";
    }

    const storageCurrency = getStorageCurrency();

    // Hide Clear BTN
    if (props.proformaInvoice.totalQuoteAmount === 0) {
      document.getElementById("clearInvoice").style.display = "none";
    } else {
      document.getElementById("clearInvoice").style.display = "block";
    }

    const totalJobUnit = Big(props.proformaInvoice.totalJobUnit).toFixed();
    const totalJobUnit0 = JSnumberToWordProcessor(String(totalJobUnit))[0];

    const serviceCharge = Big(props.proformaInvoice.serviceCharge).toFixed(1);
    const serviceCharge0 = JSnumberToWordProcessor(serviceCharge)[0];
    const serviceCharge1 = JSnumberToWordProcessor(serviceCharge)[1];

    const totalQuoteAmount = Big(
      props.proformaInvoice.totalQuoteAmount
    ).toFixed(5);
    const totalQuoteAmount0 = JSnumberToWordProcessor(totalQuoteAmount)[0];
    const totalQuoteAmount1 = JSnumberToWordProcessor(totalQuoteAmount)[1];

    const discount = Big(props.proformaInvoice.discount).toFixed();

    const vat = Big(props.proformaInvoice.vat).toFixed(1);
    const vat0 = JSnumberToWordProcessor(vat)[0];
    const vat1 = JSnumberToWordProcessor(vat)[1];

    /* ... FORMULA CALCULATIONS ... */

    // Prevent error as 0 isn't divisible
    let initProfit;
    if (props.proformaInvoice.serviceCharge === "0") {
      initProfit = "0.00";
    } else {
      initProfit = Big(props.proformaInvoice.serviceCharge)
        .div(100)
        .times(props.proformaInvoice.totalQuoteAmount)
        .toFixed(5);
    }

    // Prevent error as 0 isn't divisible
    let discountPayable;
    if (props.proformaInvoice.discount === "0") {
      discountPayable = "0.00";
    } else {
      discountPayable = Big(props.proformaInvoice.discount)
        .div(100)
        .times(initProfit)
        .toFixed(5);
    }
    const discountPayable0 = JSnumberToWordProcessor(discountPayable)[0];
    const discountPayable1 = JSnumberToWordProcessor(discountPayable)[1];

    const profit = Big(initProfit).minus(discountPayable).toFixed(5);
    const profit0 = JSnumberToWordProcessor(profit)[0];
    const profit1 = JSnumberToWordProcessor(profit)[1];

    const totalValue = Big(props.proformaInvoice.totalQuoteAmount)
      .plus(profit)
      .plus(discountPayable)
      .toFixed(5);
    const totalValue0 = JSnumberToWordProcessor(totalValue)[0];
    const totalValue1 = JSnumberToWordProcessor(totalValue)[1];

    const totalValueMinusDiscount = Big(totalValue)
      .minus(discountPayable)
      .toFixed(5);

    // Prevent error as 0 isn't divisible
    let vatPayable;
    if (props.proformaInvoice.vat === "0") {
      vatPayable = "0.00";
    } else {
      vatPayable = Big(props.proformaInvoice.vat)
        .div(100)
        .times(totalValueMinusDiscount)
        .toFixed(5);
    }
    const vatPayable0 = JSnumberToWordProcessor(vatPayable)[0];
    const vatPayable1 = JSnumberToWordProcessor(vatPayable)[1];

    const totalPayableWithVat = Big(totalValue)
      .minus(discountPayable)
      .plus(vatPayable)
      .toFixed(5);
    const totalPayableWithVat0 = JSnumberToWordProcessor(
      totalPayableWithVat
    )[0];
    const totalPayableWithVat1 = JSnumberToWordProcessor(
      totalPayableWithVat
    )[1];

    let unitDiscount;
    // if (profit === "0.00000" && totalJobUnit === "0") {
    if (Number(profit) === 0 && Number(totalJobUnit) === 0) {
      unitDiscount = "0.00";
    } else {
      // Prevent error as 0 isn't divisible
      if (props.proformaInvoice.totalJobUnit === "0") {
        unitDiscount = "0.00";
      } else {
        unitDiscount = Big(discountPayable)
          .div(props.proformaInvoice.totalJobUnit)
          .toFixed(5);
      }
    }

    if (isNaN(unitDiscount) || unitDiscount === undefined) {
      unitDiscount = "0.00";
    }
    const unitDiscount0 = JSnumberToWordProcessor(unitDiscount)[0];
    const unitDiscount1 = JSnumberToWordProcessor(unitDiscount)[1];

    let unitProfit;
    if (Number(profit) === 0 && Number(totalJobUnit) === 0) {
      unitProfit = "0.00";
    } else {
      // Prevent error as 0 isn't divisible
      if (props.proformaInvoice.totalJobUnit === "0") {
        unitProfit = "0.00";
      } else {
        unitProfit = Big(profit)
          .div(props.proformaInvoice.totalJobUnit)
          .toFixed(5);
      }
    }

    if (isNaN(unitProfit)) {
      unitProfit = "0.00";
    }
    const unitProfit0 = JSnumberToWordProcessor(unitProfit)[0];
    const unitProfit1 = JSnumberToWordProcessor(unitProfit)[1];

    let unitPrice;
    if (Number(totalPayableWithVat) === 0 && Number(totalJobUnit) === 0) {
      unitPrice = "0.00";
    } else {
      // Prevent error as 0 isn't divisible
      if (props.proformaInvoice.totalJobUnit === "0") {
        unitPrice = "0.00";
      } else {
        unitPrice = Big(totalPayableWithVat)
          .div(props.proformaInvoice.totalJobUnit)
          .toFixed(5);
      }
    }

    if (isNaN(unitPrice)) {
      unitPrice = "0.00";
    }
    const unitPrice0 = JSnumberToWordProcessor(unitPrice)[0];
    const unitPrice1 = JSnumberToWordProcessor(unitPrice)[1];

    let unit1000Price;
    if (Number(totalPayableWithVat) === 0 && Number(totalJobUnit) === 0) {
      unit1000Price = "0.00";
    } else {
      // Prevent error as 0 isn't divisible
      if (props.proformaInvoice.totalJobUnit === "0") {
        unit1000Price = "0.00";
      } else {
        unit1000Price = Big(totalPayableWithVat)
          .div(props.proformaInvoice.totalJobUnit)
          .times(1000)
          .toFixed(5);
      }
    }

    if (isNaN(unit1000Price)) {
      unit1000Price = "0.00";
    }
    const unit1000Price0 = JSnumberToWordProcessor(unit1000Price)[0];
    const unit1000Price1 = JSnumberToWordProcessor(unit1000Price)[1];

    this.setState({
      storageCurrency,

      lpoItemName: props.proformaInvoice.lpoItemName,

      totalJobUnit,
      totalJobUnit0,
      serviceCharge,
      serviceCharge0,
      serviceCharge1,
      totalQuoteAmount,
      totalQuoteAmount0,
      totalQuoteAmount1,
      discount,
      vat,
      vat0,
      vat1,
      profit,
      profit0,
      profit1,
      totalValue,
      totalValue0,
      totalValue1,
      discountPayable,
      discountPayable0,
      discountPayable1,
      vatPayable,
      vatPayable0,
      vatPayable1,
      totalPayableWithVat,
      totalPayableWithVat0,
      totalPayableWithVat1,
      unitDiscount,
      unitDiscount0,
      unitDiscount1,
      unitProfit,
      unitProfit0,
      unitProfit1,
      unitPrice,
      unitPrice0,
      unitPrice1,
      unit1000Price,
      unit1000Price0,
      unit1000Price1,
    });
  }

  proforma = () => {
    this.setState({ proforma: document.getElementById("sel1").value });
  };

  clearProformaInvoice = () => {
    if (
      JSON.parse(localStorage.getItem("proforma_invoice")).serviceCharge === "0"
    ) {
      alert("You Can't Clear An Already Emptied Invoice");
    } else {
      const confirmDelete = window.confirm(
        "Confirm Delete Of Proforma Invoice"
      );
      const nullInvoice = {
        totalQuoteAmount: "0",
        lpoItemName: "",
        totalJobUnit: "0",
        serviceCharge: "0",
        vat: "0",
        discount: "0",
      };

      if (confirmDelete) {
        this.props.addProformaInvoice(nullInvoice);

        document.getElementById("clearInvoice").style.display = "none";
        document.getElementById("recalculateInvoice").style.display = "none";
        // Set recalculateInvoice in LS to false
        localStorage.setItem("displayRecalculateBTN", false);
        // Set saveProformaInvoice in LS to false
        localStorage.setItem("saveProformaInvoice", false);
        // Hide saveProformaInvoice BTN
        document.getElementById("saveProformaInvoice").style.display = "none";

        toast.warning(`Proforma Invoice was Cleared Successfully`);
      }
    }
  };

  recalculateInvoice = () => {
    const quotations = getStorageQuotations();

    const storageProformaInvoice = getStorageProformaInvoice();

    const {
      lpoItemName,
      totalJobUnit,
      serviceCharge,
      vat,
      discount,
    } = storageProformaInvoice;

    // Only Run If There Are Expences
    if (quotations.length !== 0) {
      // Only Run If There Is An Invoice
      if (serviceCharge !== "0") {
        let totalQuoteAmount;
        if (JSON.parse(localStorage.getItem("currency")).senior === "Naira") {
          totalQuoteAmount = quotations
            .map((quotation) =>
              Big(quotation.itemUnit).times(quotation.itemPriceNaira)
            )
            .reduce((acc, value) => Big(acc).plus(value).toFixed());
        } else if (
          JSON.parse(localStorage.getItem("currency")).senior === "Dollar"
        ) {
          totalQuoteAmount = quotations
            .map((quotation) =>
              Big(quotation.itemUnit).times(quotation.itemPriceDollar)
            )
            .reduce((acc, value) => Big(acc).plus(value).toFixed());
        }

        const proformaInvioce = {
          totalQuoteAmount: Big(totalQuoteAmount).toFixed(),
          lpoItemName,
          totalJobUnit: Big(totalJobUnit).toFixed(),
          serviceCharge: Big(serviceCharge).toFixed(),
          vat: Big(vat).toFixed(),
          discount: Big(discount).toFixed(),
        };

        this.props.addProformaInvoice(proformaInvioce);

        // Show Clear Invoice
        document.getElementById("clearInvoice").style.display = "block";

        // Reveal the Recalculate BTN only when there is need for recalculation
        document.getElementById("recalculateInvoice").style.display = "none";
        // Set recalculateInvoice in LS to false
        localStorage.setItem("displayRecalculateBTN", false);
        // Set saveProformaInvoice in LS to false
        localStorage.setItem("saveProformaInvoice", true);
        // Display saveProformaInvoice BTN
        document.getElementById("saveProformaInvoice").style.display = "block";

        toast.info(`Recalculated ${lpoItemName}'s Proforma Invoice `);

        // Report Button Click To Google Analytics
        ReactGA.event({
          category: "Button",
          action: "Clicked Recalculate BTN",
          label: "QuotesCard",
        });
      } else {
        alert("You  Must Fill In Invoice Details In (STEP 2)");
      }
    } else {
      alert("You  Must Enter At Least 1 Expense In (STEP 1)");
    }
  };

  saveProformaInvoice = () => {
    // Get Saved Proforma Invoice From LS
    const getSavedProformaInvoiceInStorage = JSON.parse(
      localStorage.getItem("Saved Proforma Invoice")
    );

    // Get Current Proforma Invoice From LS
    const toggle_exchange_rate_form = JSON.parse(
      localStorage.getItem("toggle_exchange_rate_form")
    );
    const toggle_form2 = JSON.parse(localStorage.getItem("toggle_form2"));
    const currency = JSON.parse(localStorage.getItem("currency"));
    const exchange_rate = JSON.parse(localStorage.getItem("exchange_rate"));
    const proforma_invoice = JSON.parse(
      localStorage.getItem("proforma_invoice")
    );
    const quotations = JSON.parse(localStorage.getItem("quotations"));
    const quotations_length = JSON.parse(
      localStorage.getItem("quotations_length")
    );
    const displayRecalculateBTN = JSON.parse(
      localStorage.getItem("displayRecalculateBTN")
    );
    const saveProformaInvoice = JSON.parse(
      localStorage.getItem("saveProformaInvoice")
    );

    const newInvoice = {
      dateSaved: new Date().toDateString(),
      timeSaved: new Date().toLocaleTimeString(),
      toggle_exchange_rate_form,
      toggle_form2,
      currency,
      exchange_rate,
      proforma_invoice,
      quotations,
      quotations_length,
      displayRecalculateBTN,
      saveProformaInvoice,
    };

    const newInvoiceName = newInvoice.proforma_invoice.lpoItemName;

    /* If LPO Name of Previously Saved Invoice Equals New Invoice LPO Name
    Alert Caution */
    const newProformaInvoice = [];
    let duplicateOverwrite = true;

    // Check If Invoice Had Previously Been Saved
    getSavedProformaInvoiceInStorage.forEach((invoice) => {
      const invoiceToLowerCase = invoice.proforma_invoice.lpoItemName;

      if (invoiceToLowerCase.toLowerCase() === newInvoiceName.toLowerCase()) {
        // console.log("1");
        // Overwrite
        if (window.confirm(`Overwrite Saved ${newInvoiceName} LPO`) === false) {
          // console.log("2");
          duplicateOverwrite = false;
          toast.dark(`${newInvoiceName}'s LPO Invoice Was Not Updated`);
          return;
        }
      } else {
        // console.log("3");
        // NO Duplicate
        newProformaInvoice.push(invoice);
      }
    });

    /* Add New Invoice To New Proforma Invoice
    That Means Either No Duplicate Was Found OR Saving Diplicate was not accepted*/
    if (duplicateOverwrite === true) {
      // console.log("4");
      newProformaInvoice.push(newInvoice);

      // Display Toastify Indicating Invoice Was Saved
      toast(`${newInvoiceName}'s LPO Invoice Was Successfully Saved`);
      // Save New Sets Of Proforma Invoice To LS
      localStorage.setItem(
        "Saved Proforma Invoice",
        JSON.stringify(newProformaInvoice)
      );

      // Report Button Click To Google Analytics
      ReactGA.event({
        category: "Button",
        action: "Clicked Save BTN",
        label: "QuotesCard",
      });

      // Hide saveProformaInvoice BTN
      document.getElementById("saveProformaInvoice").style.display = "none";
      // Set saveProformaInvoice in LS to false
      localStorage.setItem("saveProformaInvoice", false);
    }
    // Reset duplicateOverwrite To true
    duplicateOverwrite = true;

    // Rerender Modal Accordion
    this.props.savedLPOsState();
  };

  render() {
    const {
      proforma,
      storageCurrency,

      lpoItemName,

      totalJobUnit,
      totalJobUnit0,
      serviceCharge,
      serviceCharge0,
      serviceCharge1,
      discount,
      vat,
      vat0,
      vat1,
      profit,
      profit0,
      profit1,
      totalValue,
      totalValue0,
      totalValue1,
      discountPayable,
      discountPayable0,
      discountPayable1,
      vatPayable,
      vatPayable0,
      vatPayable1,
      totalPayableWithVat,
      totalPayableWithVat0,
      totalPayableWithVat1,
      unitDiscount,
      unitDiscount0,
      unitDiscount1,
      unitProfit,
      unitProfit0,
      unitProfit1,
      unitPrice,
      unitPrice0,
      unitPrice1,
      unit1000Price0,
      unit1000Price1,
    } = this.state;

    let { proformaInvoice } = this.props;

    return (
      <div className="card my-3 table-dark" style={boxShadows}>
        <div className="card-header text-dark h5" style={boxShadows}>
          <span className="float-left mr-3">
            <i className="fa fa-file-invoice-dollar" /> PROFORMA INVOICE{" "}
          </span>
          <button
            id="clearInvoice"
            className="float-right btn btn-danger btn-sm"
            style={boxShadows}
            onClick={this.clearProformaInvoice}
          >
            Clear Invoice
          </button>
          <button
            id="recalculateInvoice"
            className="float-right btn btn-primary btn-sm mr-3"
            style={boxShadows}
            onClick={this.recalculateInvoice}
          >
            Recalculate
          </button>
          <button
            id="saveProformaInvoice"
            className="float-left btn btn-success btn-sm mr-3"
            style={boxShadows}
            onClick={this.saveProformaInvoice}
          >
            Save Invoice
          </button>
        </div>
        <div className="card-body table-responsive text-center">
          <button
            type="button"
            className="btn btn-secondary btn-sm float-left savedLPO"
            style={{
              marginTop: "-0.5rem",
              marginBottom: "0.5rem",
              boxShadow: "0 4px 8px 0 rgba(0, 0, 0, 0.2)",
            }}
            data-toggle="modal"
            data-target="#QuotesCardModal"
            onClick={() => {
              // Report Modal View To Google Analytics
              ReactGA.modalview("/Saved LPO(s) Modal");
            }}
          >
            Saved LPO(s)
          </button>
          <table
            className="table table-striped table-bordered table-hover table-sm"
            style={boxShadows}
          >
            <thead className="table-success">
              <tr>
                <th>Material</th>
                <th>Quantity</th>
                <th>Unit Price</th>
                <th>Value</th>
              </tr>
            </thead>
            <tbody>
              {/* ......... tableLinnerGraphichs Top......... */}
              <tr
                style={{
                  backgroundImage:
                    "linear-gradient(to bottom right, #d9d9d9, gray, #d9d9d9, gray, #d9d9d9)",
                }}
              >
                <td></td>
                <td></td>
                <td></td>
                <td></td>
              </tr>
              <tr>
                <td style={borderlessTable}></td>
              </tr>
              {/* ......... tableLinnerGraphichs Bottom......... */}
              <tr className="h6 text-secondary">
                <td
                  className={classnames({
                    pointerScale: Number(totalValue) !== 0,
                  })}
                  style={{
                    boxShadow: "5px 14px 10px 1px rgba(0, 0, 0, 0.3)",
                  }}
                >
                  {proformaInvoice.lpoItemName}
                </td>
                <td
                  className={classnames({
                    pointerScale: Number(totalValue) !== 0,
                  })}
                >
                  {Number(totalJobUnit) === 0 ? null : totalJobUnit0.displayNum}
                </td>
                <td
                  style={{
                    boxShadow: "5px 14px 10px 1px rgba(0, 0, 0, 0.3)",
                  }}
                >
                  {}
                </td>
                <td
                  className={classnames({
                    pointerScale: Number(totalValue) !== 0,
                  })}
                >
                  {Number(totalValue) === 0
                    ? null
                    : storageCurrency.sign + totalValue0.displayNum + "."}
                  {Number(totalValue) === 0 ? null : (
                    <small>{totalValue1.newDecimalNum}</small>
                  )}
                </td>
              </tr>
              {/* ......... tableLinnerGraphichs Top......... */}
              <tr
                style={{
                  backgroundImage:
                    "linear-gradient(to bottom right, #d9d9d9, gray, #d9d9d9, gray, #d9d9d9)",
                }}
              >
                <td></td>
                <td></td>
                <td></td>
                <td></td>
              </tr>
              <tr>
                <td style={borderlessTable}></td>
              </tr>
              {/* ......... tableLinnerGraphichs Bottom......... */}
              {Number(discount) === 0 ? null : (
                <tr className="h6 text-danger">
                  <td className="pointerScale">
                    <span>Discount</span>{" "}
                    <small className="text-muted">(@ ${discount}%)</small>
                  </td>
                  <td
                    style={{
                      boxShadow: "5px 14px 10px 1px rgba(0, 0, 0, 0.3)",
                    }}
                  ></td>
                  <td
                    className={classnames({
                      pointerScale: Number(unitDiscount) !== 0,
                    })}
                  >
                    {Number(unitDiscount) === 0
                      ? null
                      : storageCurrency.sign + unitDiscount0.displayNum + "."}
                    {Number(unitDiscount) === Infinity ||
                    Number(unitDiscount) === 0 ? null : (
                      <small>{unitDiscount1.newDecimalNum}</small>
                    )}
                  </td>
                  <td
                    className={classnames({
                      pointerScale: Number(discountPayable) !== 0,
                    })}
                    style={{
                      boxShadow: "5px 14px 10px 1px rgba(0, 0, 0, 0.3)",
                    }}
                  >
                    {Number(discountPayable) === 0
                      ? null
                      : storageCurrency.sign +
                        discountPayable0.displayNum +
                        "."}
                    {Number(discountPayable) === 0 ? null : (
                      <small>{discountPayable1.newDecimalNum}</small>
                    )}
                  </td>
                </tr>
              )}
              {/* ......... tableLinnerGraphichs Top......... */}
              <tr
                style={{
                  backgroundImage:
                    "linear-gradient(to bottom right, #d9d9d9, gray, #d9d9d9, gray, #d9d9d9)",
                }}
              >
                <td></td>
                <td></td>
                <td></td>
                <td></td>
              </tr>
              <tr>
                <td style={borderlessTable}></td>
              </tr>
              {/* ......... tableLinnerGraphichs Bottom......... */}
              <tr className="h6 text-secondary">
                <td
                  className="pointerScale"
                  style={{
                    boxShadow: "5px 14px 10px 1px rgba(0, 0, 0, 0.3)",
                  }}
                >
                  VAT{" "}
                  <small>
                    {Number(vat) === 0
                      ? null
                      : "(@ " + vat0.displayNum + "." + vat1.displayNum + "%)"}
                  </small>
                </td>
                <td></td>
                <td
                  style={{
                    boxShadow: "5px 14px 10px 1px rgba(0, 0, 0, 0.3)",
                  }}
                ></td>
                <td
                  className={classnames({
                    pointerScale: Number(vatPayable) !== 0,
                  })}
                >
                  {Number(vatPayable) === 0
                    ? null
                    : storageCurrency.sign + vatPayable0.displayNum + "."}
                  {Number(vatPayable) === 0 ? null : (
                    <small>{vatPayable1.newDecimalNum}</small>
                  )}
                </td>
              </tr>
              {/* ......... tableLinnerGraphichs Top......... */}
              <tr
                style={{
                  backgroundImage:
                    "linear-gradient(to bottom right, #d9d9d9, gray, #d9d9d9, gray, #d9d9d9)",
                }}
              >
                <td></td>
                <td></td>
                <td></td>
                <td></td>
              </tr>
              <tr>
                <td style={borderlessTable}></td>
              </tr>
              {/* ......... tableLinnerGraphichs Bottom......... */}
              <tr className="h5 text-primary">
                <td className="pointerScale">
                  TOTAL <small className="text-secondary">(VAT inc.)</small>
                </td>
                <td
                  className={classnames({
                    pointerScale: Number(totalJobUnit) !== 0,
                  })}
                  style={{
                    boxShadow: "5px 14px 10px 1px rgba(0, 0, 0, 0.3)",
                  }}
                >
                  {Number(totalJobUnit) === 0 ? null : totalJobUnit0.displayNum}
                </td>
                <td
                  className={classnames({
                    pointerScale: Number(unitPrice) !== 0,
                  })}
                >
                  {Number(unitPrice) === 0
                    ? null
                    : storageCurrency.sign + unitPrice0.displayNum + "."}
                  {Number(unitPrice) === Infinity ||
                  Number(unitPrice) === 0 ? null : (
                    <small>{unitPrice1.newDecimalNum}</small>
                  )}
                </td>
                <td
                  className={classnames({
                    pointerScale: Number(totalPayableWithVat) !== 0,
                  })}
                  style={{
                    boxShadow: "5px 14px 10px 1px rgba(0, 0, 0, 0.3)",
                  }}
                >
                  {Number(totalPayableWithVat) === 0
                    ? null
                    : storageCurrency.sign +
                      totalPayableWithVat0.displayNum +
                      "."}
                  {Number(totalPayableWithVat) === 0 ? null : (
                    <small>{totalPayableWithVat1.newDecimalNum}</small>
                  )}
                </td>
              </tr>
              {/* ......... tableLinnerGraphichs Top......... */}
              <tr
                style={{
                  backgroundImage:
                    "linear-gradient(to bottom right, #d9d9d9, gray, #d9d9d9, gray, #d9d9d9)",
                }}
              >
                <td></td>
                <td></td>
                <td></td>
                <td></td>
              </tr>
              <tr>
                <td style={borderlessTable}></td>
              </tr>
              {/* ......... tableLinnerGraphichs Bottom......... */}
              <tr className="h5 text-success">
                <td
                  className="pointerScale"
                  style={{
                    boxShadow: "5px 14px 10px 1px rgba(0, 0, 0, 0.3)",
                  }}
                >
                  PROFIT
                  <small className="text-secondary">
                    (after VAT)
                    <small>
                      {Number(serviceCharge) === 0
                        ? null
                        : " [@ " +
                          serviceCharge0.displayNum +
                          "." +
                          serviceCharge1.displayNum +
                          "%]"}
                    </small>
                  </small>
                </td>
                <td></td>
                <td
                  className={classnames({
                    pointerScale: Number(unitProfit) !== 0,
                  })}
                  style={{
                    boxShadow: "5px 14px 10px 1px rgba(0, 0, 0, 0.3)",
                  }}
                >
                  {Number(unitProfit) === 0
                    ? null
                    : storageCurrency.sign + unitProfit0.displayNum + "."}
                  {Number(unitProfit) === Infinity ||
                  Number(unitProfit) === 0 ? null : (
                    <small>{unitProfit1.newDecimalNum}</small>
                  )}
                </td>
                <td
                  className={classnames({
                    pointerScale: Number(profit) !== 0,
                  })}
                >
                  {Number(profit) === 0
                    ? null
                    : storageCurrency.sign + profit0.displayNum + "."}
                  {Number(profit) === 0 ? null : (
                    <small>{profit1.newDecimalNum}</small>
                  )}
                </td>
              </tr>
              {/* ......... tableLinnerGraphichs Top......... */}
              <tr
                style={{
                  backgroundImage:
                    "linear-gradient(to bottom right, #d9d9d9, gray, #d9d9d9, gray, #d9d9d9)",
                }}
              >
                <td></td>
                <td></td>
                <td></td>
                <td></td>
              </tr>
              {/* ......... tableLinnerGraphichs Bottom......... */}
            </tbody>
          </table>
        </div>
        <div
          className="card-footer bg-secondary text-light col-sm-11 mx-auto"
          style={(boxShadows, effects)}
        >
          {Number(unitPrice) === 0 ? null : (
            <div className="form-group" style={boxShadows}>
              <select
                className="form-control"
                id="sel1"
                onClick={(this, this.proforma)}
              >
                <option value="1">Display Unit Price</option>
                <option value="1000">Display Price Per 1,000 units</option>
                <option value="101">Display TOTAL (VAT inc.)</option>
                <option value="102">
                  PROFIT(after VAT [
                  {vat0.displayNum + "." + vat1.displayNum + "%"}])
                </option>
              </select>
            </div>
          )}
          {Number(unitPrice) === Infinity ||
          Number(unitPrice) === 0 ? null : proforma === "1" ? (
            <ul style={{ listStyleType: "none" }}>
              <li className="h5">
                <span className="h4">PROFORMA INVOICE</span>{" "}
                <hr className="bg-light" />
                {totalJobUnit0.displayNum} Pcs. of {lpoItemName} @{" "}
                {storageCurrency.sign}
                {unitPrice0.displayNum + "."}
                <small>{unitPrice1.newDecimalNum}</small>
                {Number(unitPrice0.displayNum) === 0
                  ? null
                  : " (" +
                    unitPrice0.displayWord +
                    " " +
                    storageCurrency.senior}
                {Number(unitPrice0.displayNum) === 0
                  ? " ("
                  : Number(unitPrice1.newDecimalNum) === 0
                  ? null
                  : ", and "}
                {Number(unitPrice1.newDecimalNum) === 0
                  ? null
                  : unitPrice1.newDecimalWord + " " + storageCurrency.junior}
                ) each.
              </li>
            </ul>
          ) : proforma === "1000" ? (
            <ul style={{ listStyleType: "none" }}>
              <li className="h5">
                <span className="h4">PROFORMA INVOICE</span>{" "}
                <hr className="bg-light" />
                {totalJobUnit0.displayNum} Pcs. of {lpoItemName} @{" "}
                {storageCurrency.sign}
                {unit1000Price0.displayNum + "."}
                <small>{unit1000Price1.newDecimalNum}</small>
                {Number(unit1000Price0.displayNum) === 0
                  ? null
                  : " (" +
                    unit1000Price0.displayWord +
                    " " +
                    storageCurrency.senior}
                {Number(unit1000Price0.displayNum) === 0
                  ? " ("
                  : Number(unit1000Price1.newDecimalNum) === 0
                  ? null
                  : ", and "}
                {Number(unit1000Price1.newDecimalNum) === 0
                  ? null
                  : unit1000Price1.newDecimalWord +
                    " " +
                    storageCurrency.junior}
                ) per 1000 units.
              </li>
            </ul>
          ) : proforma === "101" ? (
            <ul style={{ listStyleType: "none" }}>
              <li className="h5">
                <span className="h4">PROFORMA INVOICE</span>
                <hr className="bg-light" />
                {totalJobUnit0.displayNum} Pcs. of {lpoItemName} @{" "}
                {storageCurrency.sign}
                {totalPayableWithVat0.displayNum + "."}
                <small>{totalPayableWithVat1.newDecimalNum}</small>
                {Number(totalPayableWithVat0.displayNum) === 0
                  ? null
                  : " (" +
                    totalPayableWithVat0.displayWord +
                    " " +
                    storageCurrency.senior}
                {Number(totalPayableWithVat0.displayNum) === 0
                  ? " ("
                  : Number(totalPayableWithVat1.newDecimalNum) === 0
                  ? null
                  : ", and "}
                {Number(totalPayableWithVat1.newDecimalNum) === 0
                  ? null
                  : totalPayableWithVat1.newDecimalWord +
                    " " +
                    storageCurrency.junior +
                    ")."}{" "}
              </li>
            </ul>
          ) : (
            <ul style={{ listStyleType: "none" }}>
              <li className="h5">
                <span className="h4">PROFORMA INVOICE</span>
                <hr className="bg-light" />
                {totalJobUnit0.displayNum} Pcs. of {lpoItemName} @{" "}
                {storageCurrency.sign}
                {totalPayableWithVat0.displayNum + "."}
                <small>{totalPayableWithVat1.newDecimalNum}</small>
                {Number(totalPayableWithVat0.displayNum) === 0
                  ? null
                  : " (" +
                    totalPayableWithVat0.displayWord +
                    " " +
                    storageCurrency.senior}
                {Number(totalPayableWithVat0.displayNum) === 0
                  ? " ("
                  : Number(totalPayableWithVat1.newDecimalNum) === 0
                  ? null
                  : ", and "}
                {Number(totalPayableWithVat1.newDecimalNum) === 0
                  ? null
                  : totalPayableWithVat1.newDecimalWord +
                    " " +
                    storageCurrency.junior}
                ). <br />
                <br />
                <div className="h4 mt-3">PROFIT</div>
                <hr className="bg-light" />
                {storageCurrency.sign}
                {profit0.displayNum + "."}
                <small>{profit1.newDecimalNum}</small>
                {Number(profit0.displayNum) === 0
                  ? null
                  : " (" + profit0.displayWord + " " + storageCurrency.senior}
                {Number(profit0.displayNum) === 0
                  ? " ("
                  : Number(profit1.newDecimalNum) === 0
                  ? null
                  : ", and "}
                {Number(profit1.newDecimalNum) === 0
                  ? null
                  : profit1.newDecimalWord + " " + storageCurrency.junior}
                ).
              </li>
            </ul>
          )}
        </div>
      </div>
    );
  }
}

QuotesCard.propTypes = {
  proformaInvoice: PropTypes.object.isRequired,
  getProformaInvoice: PropTypes.func.isRequired,
  addProformaInvoice: PropTypes.func.isRequired,
  getCurrency: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => ({
  proformaInvoice: state.quotation.proformaInvoice,
});

export default connect(mapStateToProps, {
  getProformaInvoice,
  addProformaInvoice,
  getCurrency,
  savedLPOsState,
})(QuotesCard);

const boxShadows = {
  boxShadow: "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
};

const effects = {
  borderRadius: "15px 15px 50px 50px",
};

// Use This Style Props Without Border Instead of .table-responsive
const borderlessTable = {
  display: "block",
  width: "100%",
  overflowX: "auto",
  border: "none",
};
