import React from "react";

export default function QuotesCardModalAccordionLpoExpenses(props) {
  const { LPOs } = props;

  return (
    <React.Fragment>
      <table
        className="table table-dark table-bordered table-striped table-hover table-responsive"
        style={boxShadows}
      >
        <thead className="bg-success text-light">
          <tr>
            <th>Item Name</th>
            <th>Description </th>
            <th>Unit</th>
            <th>Naira ()</th>
            <th>Dollar ($)</th>
          </tr>
        </thead>
        {LPOs.expenses.map((items) => (
          <tbody key={items.id}>
            <tr>
              <td>{items.itemName}</td>
              <td>{items.itemDescription}</td>
              <td>{items.itemUnit}</td>
              <td>{Number(items.itemUnitPriceNaira).toFixed(2)}</td>
              <td>${Number(items.itemUnitPriceDollar).toFixed(2)}</td>
            </tr>
          </tbody>
        ))}
      </table>
    </React.Fragment>
  );
}

export function QuotesCardModalAccordionLpoExpensesHeader(props) {
  const { LPOs } = props;
  return (
    <React.Fragment>
      <a
        className="card-link"
        data-toggle="collapse"
        href={`#collapse${LPOs.id}`}
      >
        {LPOs.lpoItemName}
      </a>
      <small className="badge badge-secondary float-right">{LPOs.id} </small>
    </React.Fragment>
  );
}

const boxShadows = {
  boxShadow: "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
};
