import React, { Component } from "react";
// import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { HashRouter as Router, Route, Switch } from "react-router-dom";
import { Provider } from "react-redux";
import store from "./store";

import QuotesHome from "./components/quotations/QuotesHome";
import EditForm from "./components/quotations/EditForm";
import Redirect from "./components/quotations/Redirect";
import NotFound from "./components/pages/NotFound";

import FormulaModal from "./components/layouts/FormulaModal";
import QuotesCardModal from "./components/quotations/QuotesCardModal";

import ReactGA from "react-ga";

import { ToastContainer } from "react-toastify";

import "react-toastify/dist/ReactToastify.css";

import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap";
import "./fontawesome/css/all.css";

import "./App.css";

class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <Router>
          {
            // Intergrate React-ga with Google Analytics
            ReactGA.initialize("UA-171142106-1")

            // ReactGA.initialize("UA-171142106-1", {
            //   debug: true,
            //   titleCase: false,
            //   gaOptions: {
            //     userId: 123,
            //     name: "Proforma Invoice Calculator",
            //     siteSpeedSampleRate: 100,
            //   },
            // })
          }
          <ToastContainer />
          <Switch>
            <Route exact path="/" component={QuotesHome} />
            <Route exact path="/update/:id" component={EditForm} />
            <Route exact path="/redirect" component={Redirect} />
            <Route component={NotFound} />
          </Switch>
          <QuotesCardModal />
          <FormulaModal />
        </Router>
      </Provider>
    );
  }
}

export default App;
