import { SAVE_LPO_STATE, DELETE_LPO } from "./types";
import {
  savedLPOsStateStorage,
  delete4rmStorageSavedLpo,
} from "./lpoStorageActions";

export const savedLPOsState = () => async (dispatch) => {
  // Populate LS With New State
  const LPOsState = savedLPOsStateStorage();

  // Push  Selected Invoice To State
  dispatch({
    type: SAVE_LPO_STATE,
    payload: LPOsState,
  });
};

export const deleteLpo = (LPOs) => async (dispatch) => {
  // Construct New LPOs & Populate To LS
  const newlySavedProformaInvoice = delete4rmStorageSavedLpo(LPOs);
  dispatch({
    type: DELETE_LPO,
    payload: newlySavedProformaInvoice,
  });
};
