import React from "react";

export default function FormulaModal() {
  return (
    <div>
      {/* The Modal */}
      <div className="modal fade" id="myModal">
        <div className="modal-dialog modal-dialog-scrollable">
          <div className="modal-content">
            {/*  Modal Header */}
            <div className="modal-header bg-info text-light">
              <h4 className="modal-title">
                Proforma Invoice Formula <br />
                <sup style={{ color: "lightgray" }}>v4.0.0</sup>
              </h4>
              <button
                type="button"
                className="close btn-outline-danger"
                data-dismiss="modal"
              >
                &times;
              </button>
            </div>

            {/*  Modal body  */}
            <div className="modal-body">
              <ol>
                <li className="h5 text-muted">
                  "Item Name" Value
                  <div className="text-success h6 text-center mr-5">
                    <em>
                      <ins>Total Expenses + Profit + Discount</ins>
                    </em>
                  </div>
                  <ul style={{ listStyleType: "disc" }}>
                    <li>
                      Total Expenses + (((Service Charge / 100) * Total
                      Expenses) - ((Discount / 100) * ((Service Charge / 100) *
                      Total Expenses))) + ((Discount / 100) * ((Service Charge /
                      100) * Total Expenses))
                    </li>
                  </ul>
                </li>
                <hr />
                <li className="h5 text-muted">
                  Discount (Unit Price)
                  <div className="text-success h6 text-center mr-5">
                    <em>
                      <ins>Discount / Total Job Unit</ins>
                    </em>
                  </div>
                  <ul style={{ listStyleType: "disc" }}>
                    <li>
                      ((Discount / 100) * ((Service Charge / 100) * Total
                      Expenses)) / Total Job Unit
                    </li>
                  </ul>
                </li>
                <hr />
                <li className="h5 text-muted">
                  Discount (Total Amount)
                  <div className="text-success h6 text-center mr-5">
                    <em>
                      <ins>Discounted % * Profit-Before-Discount</ins>
                    </em>
                  </div>
                  <ul style={{ listStyleType: "disc" }}>
                    <li>
                      (Discount / 100) * ((Service Charge / 100) * Total
                      Expenses)
                    </li>
                  </ul>
                </li>
                <hr />
                <li className="h5 text-muted">
                  VAT
                  <div className="text-success h6 text-center mr-5">
                    <em>
                      <ins>VAT % * (Total Value - Discount)</ins>
                    </em>
                  </div>
                  <ul style={{ listStyleType: "disc" }}>
                    <li>
                      (VAT / 100) * ((Total Expenses + (((Service Charge / 100)
                      * Total Expenses) - ((Discount / 100) * ((Service Charge /
                      100) * Total Expenses))) + ((Discount / 100) * ((Service
                      Charge / 100) * Total Expenses))) - ((Discount / 100) *
                      ((Service Charge / 100) * Total Expenses)))
                    </li>
                  </ul>
                </li>
                <hr />
                <li className="h5 text-muted">
                  Proforma Invoice (Price Per Unit)
                  <div className="text-success h6 text-center mr-5">
                    <em>
                      <ins>Total LPO Amount / Total Job Unit</ins>
                    </em>
                  </div>
                  <ul style={{ listStyleType: "disc" }}>
                    <li>
                      ((Total Expenses + (((Service Charge / 100) * Total
                      Expenses) - ((Discount / 100) * ((Service Charge / 100) *
                      Total Expenses))) + ((Discount / 100) * ((Service Charge /
                      100) * Total Expenses))) - ((Discount / 100) * ((Service
                      Charge / 100) * Total Expenses)) + ((VAT / 100) * ((Total
                      Expenses + (((Service Charge / 100) * Total Expenses) -
                      ((Discount / 100) * ((Service Charge / 100) * Total
                      Expenses))) + ((Discount / 100) * ((Service Charge / 100)
                      * Total Expenses))) - ((Discount / 100) * ((Service Charge
                      / 100) * Total Expenses))))) / Total Job Unit
                    </li>
                  </ul>
                </li>
                <hr />
                <li className="h5 text-muted">
                  Proforma Invoice (Total Amount)
                  <div className="text-success h6 text-center mr-5">
                    <em>
                      <ins>Total Value - Discount + VAT</ins>
                    </em>
                  </div>
                  <ul style={{ listStyleType: "disc" }}>
                    <li>
                      (Total Expenses + (((Service Charge / 100) * Total
                      Expenses) - ((Discount / 100) * ((Service Charge / 100) *
                      Total Expenses))) + ((Discount / 100) * ((Service Charge /
                      100) * Total Expenses))) - ((Discount / 100) * ((Service
                      Charge / 100) * Total Expenses)) + ((VAT / 100) * ((Total
                      Expenses + (((Service Charge / 100) * Total Expenses) -
                      ((Discount / 100) * ((Service Charge / 100) * Total
                      Expenses))) + ((Discount / 100) * ((Service Charge / 100)
                      * Total Expenses))) - ((Discount / 100) * ((Service Charge
                      / 100) * Total Expenses))))
                    </li>
                  </ul>
                </li>
                <hr />
                <li className="h5 text-muted">
                  Profit After VAT (Profit Per Unit)
                  <div className="text-success h6 text-center mr-5">
                    <em>
                      <ins>Profit / Total Job Unit</ins>
                    </em>
                  </div>
                  <ul style={{ listStyleType: "disc" }}>
                    <li>
                      ((((Service Charge / 100) * Total Expenses) - ((Discount /
                      100) * ((Service Charge / 100) * Total Expenses))) -
                      ((Discount / 100) * ((Service Charge / 100) * Total
                      Expenses))) / Total Job Unit
                    </li>
                  </ul>
                </li>
                <hr />
                <li className="h5 text-muted">
                  Profit After VAT(Total Profit)
                  <div className="text-success h6 text-center mr-5">
                    <em>
                      <ins>Service Charge % * Total Expenses - Discount</ins>
                    </em>
                  </div>
                  <ul style={{ listStyleType: "disc" }}>
                    <li>
                      (((Service Charge / 100) * Total Expenses) - ((Discount /
                      100) * ((Service Charge / 100) * Total Expenses))) -
                      ((Discount / 100) * ((Service Charge / 100) * Total
                      Expenses))
                    </li>
                  </ul>
                </li>
              </ol>
            </div>

            {/* Modal footer */}
            <div
              className="modal-footer"
              style={{ backgroundColor: "lightgray" }}
            >
              <button
                type="button"
                className="btn btn-danger"
                data-dismiss="modal"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
