import React, { Component } from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import {
  getQuotations,
  deleteQuotation,
  clearAllQuotations,
  addProformaInvoice,
} from "../../actions/quotationTypes";
import { connect } from "react-redux";

import { JSnumberToWordProcessor } from "../JSnumberToWordProcessor/JSnumberToWordProcessor";
import { getStorageQuotations } from "../../actions/quotationStorageActions";
import { getStorageCurrency } from "../../actions/currencyStorageActions";

import Big from "big.js";

import { toast } from "react-toastify";

class Quotes extends Component {
  state = {
    storageCurrency: {},

    totalPrice: "",
    totalPrice0: "",
    totalPrice1: "",
    quotes: [],
  };
  componentDidMount() {
    this.props.getQuotations();

    // Hide Clear Btn If No Expenses
    if (JSON.parse(localStorage.getItem("quotations")).length === 0) {
      document.getElementById("clearAllInvoice").style.display = "none";
    } else {
      document.getElementById("clearAllInvoice").style.display = "block";
    }
  }

  UNSAFE_componentWillReceiveProps(props) {
    const storageCurrency = getStorageCurrency();

    const { quotations } = props;

    if (quotations !== undefined) {
      const allTotalPriceArray = [];

      const quotes = quotations.map((quotation) => {
        const itemId = quotation.id;
        const itemName = quotation.itemName;
        const itemDescription = quotation.itemDescription;
        const itemUnit = quotation.itemUnit;
        const itemUnit0 = JSnumberToWordProcessor(String(itemUnit))[0];
        // const itemPrice = quotation.itemPriceNaira;
        let itemPrice;
        if (JSON.parse(localStorage.getItem("currency")).senior === "Naira") {
          itemPrice = quotation.itemPriceNaira;
        } else if (
          JSON.parse(localStorage.getItem("currency")).senior === "Dollar"
        ) {
          itemPrice = quotation.itemPriceDollar;
        }
        const itemPrice0 = JSnumberToWordProcessor(String(itemPrice))[0];
        let itemPrice1 = JSnumberToWordProcessor(String(itemPrice))[1];

        if (itemPrice1 === undefined) {
          itemPrice1 = JSnumberToWordProcessor(String("0.00"))[1];
        }

        const itemPriceTotal = Big(itemUnit).times(itemPrice).toFixed();
        const itemPriceTotalJSNum = JSnumberToWordProcessor(
          String(itemPriceTotal)
        );
        const itemPriceTotal0 = itemPriceTotalJSNum[0];
        let itemPriceTotal1;
        if (itemPriceTotalJSNum.length === 2) {
          itemPriceTotal1 = itemPriceTotalJSNum[1];
        } else {
          itemPriceTotal1 = JSnumberToWordProcessor(String("0.00"))[1];
        }

        allTotalPriceArray.push(itemPriceTotal);

        return {
          itemId,
          itemName,
          itemDescription,
          itemUnit,
          itemUnit0,
          itemPrice,
          itemPrice0,
          itemPrice1,
          itemPriceTotal,
          itemPriceTotal0,
          itemPriceTotal1,
        };
      });

      // Calculate Total Price
      let totalPrice = "0.00";
      let totalPrice0 = JSnumberToWordProcessor(String(totalPrice))[0];
      let totalPrice1 = JSnumberToWordProcessor(String(totalPrice))[1];

      if (props.quotations.length !== 0) {
        let reduceAllTotalPriceArray = allTotalPriceArray.reduce((acc, arr) => {
          return Big(acc).plus(arr).toFixed(2);
        });

        totalPrice = reduceAllTotalPriceArray;
        totalPrice0 = JSnumberToWordProcessor(String(totalPrice))[0];
        // Only Change totalPrice Value If reduceAllTotalPriceArray Has Decimal Number
        if (JSnumberToWordProcessor(String(totalPrice)).length === 2) {
          totalPrice1 = JSnumberToWordProcessor(String(totalPrice))[1];
        }
      }

      this.setState({
        storageCurrency,

        totalPrice,
        totalPrice0,
        totalPrice1,

        quotes,
      });
    }
  }

  resetQuotes = () => {
    const nullInvoice = {
      totalQuoteAmount: "0",
      lpoItemName: "",
      totalJobUnit: "0",
      serviceCharge: "0",
      vat: "0",
      discount: "0",
    };

    // Clear Expenses & Invoice
    this.props.addProformaInvoice(nullInvoice);
    this.props.clearAllQuotations();

    // Hide clearAllInvoice, Recalculate  & Clear Btn Under Invoice
    document.getElementById("clearInvoice").style.display = "none";
    document.getElementById("clearAllInvoice").style.display = "none";
    document.getElementById("recalculateInvoice").style.display = "none";
    // Hide saveProformaInvoice BTN
    document.getElementById("saveProformaInvoice").style.display = "none";
    // Set recalculateInvoice in LS to false
    localStorage.setItem("displayRecalculateBTN", false);
    // Set saveProformaInvoice in LS to false
    localStorage.setItem("saveProformaInvoice", false);
  };

  clearAllInvoice = () => {
    const confirmDelete = window.confirm(
      "Confirm Delete Of All Expenses With Proforma Invoice"
    );

    if (confirmDelete) {
      this.resetQuotes();

      toast.warning(`Successfully Cleared All Expenses With Proforma Invoice`);
    }
  };

  deleteQuote = (id) => {
    const confirmDelete = window.confirm("Confirm Delete Of Expense");
    // Dispatch
    if (confirmDelete) {
      this.props.deleteQuotation(id);

      const quotationsLength = getStorageQuotations();

      if (quotationsLength.length === 0) {
        this.resetQuotes();
      }
      // Reveal the Recalculate BTN only when there is need for recalculation
      document.getElementById("recalculateInvoice").style.display = "block";
      // Set recalculateInvoice in LS to false
      localStorage.setItem("displayRecalculateBTN", true);
      // Hide saveProformaInvoice BTN
      document.getElementById("saveProformaInvoice").style.display = "none";
      // Set saveProformaInvoice in LS to false
      localStorage.setItem("saveProformaInvoice", false);
    }
  };

  render() {
    const {
      storageCurrency,
      totalPrice,
      totalPrice0,
      totalPrice1,
      quotes,
    } = this.state;

    return (
      <React.Fragment>
        <table
          className="table table-striped table-bordered text-center"
          // Use This Style Props Without Border Instead of .table-responsive
          style={{
            display: "block",
            width: "100%",
            overflowX: "auto",
            border: "none",
          }}
        >
          <thead className="thead-inverse">
            <tr className="table-borderless text-center">
              <th
                style={{
                  backgroundColor: "firebrick",
                  color: "white",
                }}
              >
                EXPENSES
              </th>
              <th></th>
              <th></th>
              <th></th>
              <th
                id="clearAllInvoice"
                className="text-center btn btn-sm btn-danger float-right"
                style={boxShadows}
                onClick={this.clearAllInvoice}
              >
                Clear All
              </th>
            </tr>
            <tr className="table-primary">
              <th>Item Name</th>
              <th>Description</th>
              <th>Unit</th>
              <th>Unit Price</th>
              <th>Total Price</th>
            </tr>
          </thead>
          <tbody>
            {quotes.map((quotation) => (
              <tr key={quotation.itemId}>
                <td>{quotation.itemName}</td>
                <td>{quotation.itemDescription}</td>
                <td>{quotation.itemUnit0.displayNum}</td>
                <td>
                  {storageCurrency.sign}
                  {quotation.itemPrice0.displayNum + "."}
                  <small>{quotation.itemPrice1.newDecimalNum}</small>{" "}
                </td>
                <td>
                  {storageCurrency.sign}
                  {quotation.itemPriceTotal0.displayNum + "."}
                  <small>{quotation.itemPriceTotal1.newDecimalNum}</small>{" "}
                </td>
                <td>
                  <Link to={`/update/${quotation.itemId}`}>
                    <i
                      className="fa fa-pencil-alt text-success editDeletePointerScale"
                      style={{ cursor: "pointer" }}
                    />{" "}
                  </Link>
                  {""}
                  <i
                    className="fa fa-times text-danger editDeletePointerScale"
                    style={{ cursor: "pointer" }}
                    onClick={this.deleteQuote.bind(this, quotation.itemId)}
                  />
                </td>
              </tr>
            ))}
            {/* ......... tableLinnerGraphichs Top......... */}
            <tr className="table-sm">
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
            </tr>
            {/* ......... tableLinnerGraphichs Bottom......... */}
            <tr className="h5 text-secondary">
              <td className="text-primary pointerScale" style={boxShadows}>
                TOTAL
              </td>
              <td></td>
              <td></td>
              <td></td>
              {Number(totalPrice) === 0 ? (
                <td className="text-success pointerScale" style={boxShadows}>
                  {storageCurrency.sign}
                  {totalPrice0.displayNum + "."}
                  <small>{totalPrice1.newDecimalNum}</small>{" "}
                </td>
              ) : (
                <td className="text-danger pointerScale" style={boxShadows}>
                  {storageCurrency.sign}
                  {totalPrice0.displayNum + "."}
                  <small>{totalPrice1.newDecimalNum}</small>{" "}
                </td>
              )}
            </tr>
            {/* ......... tableLinnerGraphichs Top......... */}
            <tr className="table-sm">
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
            </tr>
            {/* ......... tableLinnerGraphichs Bottom......... */}
          </tbody>
        </table>
      </React.Fragment>
    );
  }
}

Quotes.propType = {
  quotations: PropTypes.array.isRequired,
  getQuotations: PropTypes.func.isRequired,
  deleteQuotation: PropTypes.func.isRequired,
  clearAllQuotations: PropTypes.func.isRequired,
  addProformaInvoice: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => ({
  quotations: state.quotation.quotations,
});

export default connect(mapStateToProps, {
  getQuotations,
  deleteQuotation,
  clearAllQuotations,
  addProformaInvoice,
})(Quotes);

const boxShadows = {
  boxShadow: "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
};
