import React from "react";

import { v4 as uuid } from "uuid";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { addQuotation } from "../../actions/quotationTypes";

import {
  addCurrency,
  calculateNairaExchangeRate,
  calculateDollarExchangeRate,
} from "../../actions/currencyTypes";
import {
  getStorageCurrency,
  getStorageExchangeRate,
  toggleStorageExchangeRateForm,
  toggleStorageForm2,
} from "../../actions/currencyStorageActions";
import {
  getStorageQuotations,
  getStorageProformaInvoice,
} from "../../actions/quotationStorageActions";

import Big from "big.js";

class Form extends React.Component {
  state = {
    itemName: "",
    itemDescription: "",
    itemUnit: "",
    itemPrice: "",
    currencySenior: "",
  };

  componentDidMount() {
    // set toggle For ExchangeRate Form
    toggleStorageExchangeRateForm();
    toggleStorageForm2();

    let currency = getStorageCurrency();

    // Set Currency Flag Display
    if (currency.senior === "Naira") {
      // addCurrency
      document.getElementById("nigCurr").style.opacity = "1";
      document.getElementById("usaCurr").style.opacity = "0.5";
    } else if (currency.senior === "Dollar") {
      document.getElementById("usaCurr").style.opacity = "1";
      document.getElementById("nigCurr").style.opacity = "0.5";
    }

    this.setState({ currencySenior: currency.senior });
  }

  nigCurrency = () => {
    let initCurrency = getStorageCurrency();
    let getAllQuotation = getStorageQuotations();

    const exchangeRate = getStorageExchangeRate();
    const naira = Number(exchangeRate.naira);
    const dollar = Number(exchangeRate.dollar);

    const currency = {
      senior: "Naira",
      junior: "kobo",
      sign: "",
      abbrevation: "NGN",
    };

    document.getElementById("nigCurr").style.opacity = "1";
    document.getElementById("usaCurr").style.opacity = "0.5";

    this.props.addCurrency(currency);
    this.setState({ currencySenior: currency.senior });

    if (initCurrency.senior !== currency.senior) {
      if (getAllQuotation.length !== 0) {
        this.props.calculateNairaExchangeRate(naira, dollar);

        // Recalculate Proforma Invoice
        const getProformaInvoice = getStorageProformaInvoice();
        if (getProformaInvoice.totalQuoteAmount !== 0) {
          document.getElementById("recalculateInvoice").click();
        }
      }
    }
  };

  usaCurrency = () => {
    let initCurrency = getStorageCurrency();
    let getAllQuotation = getStorageQuotations();

    const exchangeRate = getStorageExchangeRate();
    const naira = Number(exchangeRate.naira);
    const dollar = Number(exchangeRate.dollar);

    const currency = {
      senior: "Dollar",
      junior: "cent",
      sign: "$",
      abbrevation: "USD",
    };

    document.getElementById("usaCurr").style.opacity = "1";
    document.getElementById("nigCurr").style.opacity = "0.5";

    this.props.addCurrency(currency);
    this.setState({ currencySenior: currency.senior });

    if (initCurrency.senior !== currency.senior) {
      if (getAllQuotation.length !== 0) {
        this.props.calculateDollarExchangeRate(naira, dollar);

        // Recalculate Proforma Invoice
        const getProformaInvoice = getStorageProformaInvoice();
        if (getProformaInvoice.totalQuoteAmount !== 0) {
          document.getElementById("recalculateInvoice").click();
        }
      }
    }
  };

  valueChanged = (e) => {
    this.setState({ [e.target.name]: e.target.value });
  };

  valueSubmited = (e) => {
    e.preventDefault();

    let { itemName, itemDescription, itemUnit, itemPrice } = this.state;

    /*
     ** Add '0' To ItemPrice If It's e.g '.88'
     ** ItemPrice Would Now Be '0.88'
     */
    let itemPriceSplit = itemPrice.split(".");
    if (itemPriceSplit.length === 2) {
      if (itemPriceSplit[0] === "") {
        itemPrice = "0." + itemPriceSplit[1];
      }
    }

    // Item Price Must Be a Number
    if (isNaN(itemPrice)) {
      alert("Enter A Valid Number As Item Amount");
    } else {
      // Set itemUnit Default Value
      if (itemUnit === "") {
        itemUnit = "1";
      }

      /*  Split Item Price Into itemPriceNaira & itemPriceDollar
       ** Calculate Both Item Price Based On Exchange Rate
       */
      let itemPriceNaira;
      let itemPriceDollar;

      const currency = JSON.parse(localStorage.getItem("currency")).senior;
      const ngn = Number(
        JSON.parse(localStorage.getItem("exchange_rate")).naira
      );
      const usd = Number(
        JSON.parse(localStorage.getItem("exchange_rate")).dollar
      );

      let exchangeRate;
      if (ngn > usd) {
        exchangeRate = ngn / usd;
      } else if (usd > ngn) {
        exchangeRate = usd / ngn;
      }

      if (currency === "Naira") {
        itemPriceNaira = itemPrice;
        /*
         **  If New Exchange Rate Ratio Is Higher Than Storage Exchange Rate Ratio,
         **  Divide The New Exchange Rate Ratio With Naira To Get Dollar Value.
         **  This Occures If Dollar Value Is Greater Than Naira Value, Else Vise-Visa
         */
        itemPriceDollar =
          usd < ngn
            ? Big(itemPriceNaira).div(exchangeRate).toFixed()
            : Big(itemPriceNaira).times(exchangeRate).toFixed();
      } else if (currency === "Dollar") {
        itemPriceDollar = itemPrice;
        /*
         **  If New Exchange Rate Ratio Is Higher Than Storage Exchange Rate Ratio,
         **  Multiply The New Exchange Rate Ratio With Dollar To Get Naira Value.
         **  This Occures If Dollar Value Is Greater Than Naira Value, Else Vise-Visa
         */
        itemPriceNaira =
          usd < ngn
            ? Big(itemPriceDollar).times(exchangeRate).toFixed()
            : Big(itemPriceDollar).div(exchangeRate).toFixed();
      }

      const newQuotationItem = {
        id: uuid(),
        itemName,
        itemDescription,
        itemUnit,
        itemPriceNaira,
        itemPriceDollar,
      };

      this.setState({
        itemName: "",
        itemDescription: "",
        itemUnit: "",
        itemPrice: "",
      });

      this.props.addQuotation(newQuotationItem);

      // Display clearAllInvoice BTN
      document.getElementById("clearAllInvoice").style.display = "block";

      // Reveal the Recalculate BTN only when there is need for recalculation
      document.getElementById("recalculateInvoice").style.display = "block";
      // Hide saveProformaInvoice BTN
      document.getElementById("saveProformaInvoice").style.display = "none";
    }
  };

  toggleForm = () => {
    // Colapse Exchange Rate Form OnFocus
    if (toggleStorageExchangeRateForm() === true) {
      document.getElementById("setExchangeRateBtn").click();
    }

    // Colapse Form2 On Focus
    if (toggleStorageForm2() === true) {
      document.getElementById("generateInvoice").click();
    }
  };

  render() {
    const {
      itemName,
      itemDescription,
      itemPrice,
      itemUnit,
      currencySenior,
    } = this.state;

    const {
      placeholder1,
      inputGroupText1,
      placeholder2,
      inputGroupText2,
      placeholder4,
      inputGroupText4,
    } = this.props;
    return (
      <form onSubmit={this.valueSubmited} onFocus={this.toggleForm}>
        <div className="input-group mb-3 boxShadows" style={boxShadows}>
          <div className="input-group-prepend" style={boxShadows}>
            <span className="input-group-text">{inputGroupText1}</span>
          </div>
          <input
            required
            type="text"
            name="itemName"
            className="form-control"
            placeholder={placeholder1}
            value={itemName}
            onChange={this.valueChanged}
          />
        </div>

        <div className="input-group mb-3" style={boxShadows}>
          <input
            type="text"
            name="itemDescription"
            className="form-control"
            placeholder={placeholder2}
            value={itemDescription}
            onChange={this.valueChanged}
          />
          <div className="input-group-append">
            <span className="input-group-text">{inputGroupText2}</span>
          </div>
        </div>

        <div className="input-group mb-3 boxShadows" style={boxShadows}>
          <div className="input-group-prepend">
            <span
              id="nigCurr"
              className="input-group-text nigeriaCurrencyImage btn btn-outline-success form-control"
              onClick={this.nigCurrency}
            >
              <input type="button" />
            </span>
            <span
              id="usaCurr"
              className="input-group-text usaCurrencyImage btn btn-outline-success form-control"
              onClick={this.usaCurrency}
            >
              <input type="button" className="usaCurrencyImage2" />
            </span>
          </div>
          <input
            required
            type="number"
            step="0.00005"
            name="itemPrice"
            className="form-control"
            placeholder={"Enter Amount In " + currencySenior}
            value={itemPrice}
            onChange={this.valueChanged}
          />
          <div className="input-group-prepend" style={boxShadows}>
            <span className="input-group-text">{inputGroupText4}</span>
          </div>
          <input
            type="number"
            name="itemUnit"
            className="form-control"
            placeholder={placeholder4}
            value={itemUnit}
            onChange={this.valueChanged}
          />
        </div>
        <input
          type="submit"
          value="Submit"
          className="form-control btn btn-secondary"
          style={boxShadows}
        />
      </form>
    );
  }
}

Form.propTypes = {
  quotations: PropTypes.array.isRequired,
  addQuotation: PropTypes.func.isRequired,
  addCurrency: PropTypes.func.isRequired,
  calculateNairaExchangeRate: PropTypes.func.isRequired,
  calculateDollarExchangeRate: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => ({
  quotations: state.quotation.quotations,
});

export default connect(mapStateToProps, {
  addQuotation,
  addCurrency,
  calculateNairaExchangeRate,
  calculateDollarExchangeRate,
})(Form);

const boxShadows = {
  boxShadow: "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
};
