import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import PropTypes from "prop-types";

import { savedLPOsState, deleteLpo } from "../../actions/lpoTypes";
import { pushUploadedLpo2Storage } from "../../actions/lpoStorageActions";

import ReactGA from "react-ga";

import { toast } from "react-toastify";

class QuotesCardModalAccordionLpoDetails extends Component {
  state = {
    LPOs: [],
    LPOsArr: [],
  };

  uploadLpo(LPOs, defaultSavedProformaInvoice) {
    // Get Current Proforma Invoice Name From LS
    const currentInvoiceName = JSON.parse(
      localStorage.getItem("proforma_invoice")
    );

    const confirmOverwrite = window.confirm(
      `Ensure You Save Processed ${currentInvoiceName.lpoItemName} LPO Details On Home Page Before Overwriting With ${LPOs.lpoItemName} LPO; Else Processed ${currentInvoiceName.lpoItemName} LPO Details On Home Page Would Be Lost` +
        "\n \n Confirming Overwrite..."
    );

    if (confirmOverwrite) {
      defaultSavedProformaInvoice.forEach((invoice) => {
        if (invoice.proforma_invoice.lpoItemName === LPOs.lpoItemName) {
          pushUploadedLpo2Storage(invoice);

          toast.success(
            `${LPOs.lpoItemName}'s Proforma Invoice Was Successfully Uploaded`
          );

          // Report Button Click To Google Analytics
          ReactGA.event({
            category: "Button",
            action: "Upload Saved LPO To Home (Clicked Upload LPO BTN)",
          });

          // Close Modal
          document.getElementById("closeModal").click();

          // Rerender Home Page
          this.props.history.push("/redirect");
        }
      });
    }
  }

  deleteLpo(LPOs) {
    const confirmDelete = window.confirm(
      `Confirm Delete Of ${LPOs.lpoItemName} LPO \n \n ${LPOs.lpoItemName} Can't be retrived Once Deleted`
    );

    if (confirmDelete) {
      this.props.deleteLpo(LPOs);

      this.props.savedLPOsState();

      // Report Button Click To Google Analytics
      ReactGA.event({
        category: "Button",
        action: "Deleted Saved LPO (Clicked Delete LPO BTN)",
      });

      toast.error(`${LPOs.lpoItemName} LPO Was Successfully Deleted`);
    }
  }

  render() {
    const {
      defaultSavedProformaInvoice,
      LPOs,
      dateSaved,
      timeSaved,
    } = this.props;

    return (
      <React.Fragment>
        <div>
          <button
            onClick={this.uploadLpo.bind(
              this,
              LPOs,
              defaultSavedProformaInvoice
            )}
            className="btn btn-sm btn-outline-success"
            style={boxShadows}
          >
            Upload LPO
          </button>
          <button
            onClick={this.deleteLpo.bind(
              this,
              LPOs,
              defaultSavedProformaInvoice
            )}
            className="btn btn-sm btn-outline-danger float-right"
            style={boxShadows}
          >
            Delete LPO
          </button>
          <hr style={boxShadows} />
          <div className="mt-4 mb-5">
            <sup className="text-muted float-left">{dateSaved}</sup>
            <sup className="text-muted float-right">{timeSaved}</sup>
          </div>
        </div>
        <div>
          <h4 className="text-muted text-center">
            <ins>{LPOs.lpoItemName}'s LPO</ins>
          </h4>
          <ul style={{ fontWeight: "bold" }}>
            <li>
              Exchange Rate:{" "}
              <span style={{ fontWeight: "500" }} className="text-muted">
                ${LPOs.dollar} to {LPOs.naira}
              </span>
            </li>
            <li>
              Total Job Unit:{" "}
              <span style={{ fontWeight: "500" }} className="text-muted">
                {LPOs.totalJobUnit}
              </span>
            </li>
            <li>
              Service Charge:{" "}
              <span style={{ fontWeight: "500" }} className="text-muted">
                {LPOs.serviceCharge}%
              </span>
            </li>
            <li>
              VAT:{" "}
              <span style={{ fontWeight: "500" }} className="text-muted">
                {LPOs.vat}%
              </span>
            </li>
            <li>
              Discount:{" "}
              <span style={{ fontWeight: "500" }} className="text-muted">
                {LPOs.discount}%
              </span>
            </li>
          </ul>
        </div>
      </React.Fragment>
    );
  }
}

QuotesCardModalAccordionLpoDetails.propType = {
  savedLPOsInState: PropTypes.array.isRequired,
  newlySavedLPOsAfterDelete: PropTypes.array.isRequired,
};

const mapStateToProps = (state) => ({
  savedLPOsInState: state.quotation.savedLPOsInState,
  newlySavedLPOsAfterDelete: state.quotation.newlySavedLPOsAfterDelete,
});

export default connect(mapStateToProps, {
  savedLPOsState,
  deleteLpo,
})(withRouter(QuotesCardModalAccordionLpoDetails));

const boxShadows = {
  boxShadow: "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
};
